.education {
    width: 100%;
    height: 100vh;
    background: linear-gradient(to right, #6aa4e6 0%, #6aa4e6 40%, white 40%, white 100%);
}

.education .container {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.education .top {
    margin-top: 7rem;
}
.education .bottom {
    display: flex;
    justify-content: center;
    width: 100%;
    max-width: 600px;
    margin-bottom: 4rem;
}

@media screen and (max-width:540px) {
    .education .container .bottom {
        flex-direction: column;
        align-items: center;
    }
}